<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <div class="container" id="top-set">
      <div class="row justify-content-center">
        <div class="col-md-8">
          <form>
            <div class="form-group mt-3">
              <label></label>
              <input type="text" class="form-control" placeholder=" or IP" v-model="search">
              <small></small>
            </div>
          </form>
          <div class="form-group">
            <label for="feature"></label>
            <select class="form-control" id="feature" @change="filterUser" v-model="selected">
              <option disabled value="">---  ---</option>
              <option :value="item" v-for="(item, index) in selectData" :key="index">
                {{ item }}
              </option>
            </select>
          </div>
          <small>{{ dataLength }} </small>
        </div>
      </div>
      <div class="row">
        <div class="col-xl-4 col-md-6 col-sm-12 d-flex justify-content-center pt-2" v-for="(item, index) in filteredList" :key="index">
          <div class="card card-box w-100">
            <div class="card-body">
              <h5 class="card-title d-inline">
                <span>{{ item.first_name }} {{ item.last_name }} </span>
              </h5>
              <p><span>{{ item.gender }}</span></p>
              <p v-if="item.email != null"><span>{{ item.email }}</span></p>
              <p v-else><span></span></p>
              <p class="high-item pt-2">{{ item.description }}</p>
              <p>IP<span>{{ item.ip_address }}</span></p>
              <p><span>{{ item.login_at }}</span></p>
            </div>
            <div class="card-footer">
              <button class="btn btn-hex" @click.prevent="clickItem">Click me</button>
            </div>
          </div>
        </div>
        <div v-scroll-to="'#top-set'" class="gotopIcon" id="gotop" style="display:none">
          <i class="fas fa-arrow-alt-circle-up fa-4x"></i>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/* global $ */

import Swal from 'sweetalert2/dist/sweetalert2.js'

export default {
  data () {
    return {
      allData: [],
      templateData: [],
      search: '',
      dataLength: '',
      isLoading: false,
      selected: '',
      selectData: ['', '', '']
    }
  },
  methods: {
    clickItem () {
      let timerInterval
      Swal.fire({
        title: '><',
        html: ' <b></b> .',
        timer: 4000,
        timerProgressBar: true,
        willOpen: () => {
          Swal.showLoading()
          timerInterval = setInterval(() => {
            const content = Swal.getContent()
            if (content) {
              const b = content.querySelector('b')
              if (b) {
                b.textContent = Swal.getTimerLeft()
              }
            }
          }, 100)
        },
        onClose: () => {
          clearInterval(timerInterval)
        }
      }).then((result) => {
        /* Read more about handling dismissals below */
        if (result.dismiss === Swal.DismissReason.timer) {
          console.log('I was closed by the timer')
        }
      })
    },
    filterUser () {
      const cacheData = []
      this.allData.forEach((item) => {
        switch (this.selected) {
          case '':
            cacheData.push(item)
            this.templateData = cacheData
            break
          case '':
            if (item.gender === 'Male') {
              cacheData.push(item)
              this.templateData = cacheData
            }
            break
          case '':
            if (item.gender === 'Female') {
              cacheData.push(item)
              this.templateData = cacheData
            }
            break
        }
      })
    }
  },
  computed: {
    filteredList () {
      return this.templateData.filter((res) => {
        return res.first_name.match(this.search) || res.ip_address.match(this.search)
      })
    }
  },
  mounted () {
    function showBtnCondition () {
      if ($(this).scrollTop() > 1200) {
        $('#gotop').fadeIn()
      } else {
        $('#gotop').fadeOut()
      }
    }
    $(window).scroll(showBtnCondition)
  },
  created () {
    const api = 'https://run.mocky.io/v3/9d5f33ec-0d2f-4743-aac9-5aef32f7badf'
    this.isLoading = true
    this.$http.get(api).then((res) => {
      this.allData = res.data
      this.dataLength = res.data.length
      this.isLoading = false
      this.templateData = JSON.parse(JSON.stringify(this.allData))
    })
  }
}
</script>
